.label {
  display: grid;
  grid-template-columns: 1fr auto auto;
  border-radius: var(--radius_sm);
  padding: var(--button_medium);
  background: #f4f7fa;
  font-family: VelaSans-Regular;
}

.label {
  box-shadow: var(--shadow);
}

@media (max-width: 1140px) {
  .label {
    width: auto;
  }
}

@media (max-width: 414px) {
  .label {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.input:focus .label {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.input {
  border: none;
  background: #f4f7fa;
}

.button {
  font-family: VelaSans-Medium;
}

.button__remove {
  width: var(--input__buttons);
  height: var(--input__buttons);
  opacity: 0;
  visibility: hidden;
  user-select: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='8' height='8' viewBox='0 0 8 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fillRule='evenodd' clipRule='evenodd' d='M7.745 1.24028C7.99902 0.986258 7.99902 0.574402 7.745 0.320377C7.49097 0.066352 7.07912 0.066352 6.82509 0.320377L4.06538 3.08009L1.24028 0.255001C0.986258 0.000975884 0.574402 0.000975463 0.320377 0.255001C0.0663521 0.509026 0.0663527 0.920881 0.320377 1.17491L3.14547 4L0.320377 6.82509C0.0663514 7.07912 0.0663514 7.49097 0.320376 7.745C0.574401 7.99902 0.986258 7.99902 1.24028 7.745L4.06537 4.9199L6.82509 7.67962C7.07912 7.93365 7.49097 7.93365 7.745 7.67962C7.99902 7.4256 7.99902 7.01374 7.745 6.75972L4.98528 4L7.745 1.24028Z' fill='%2391969B'/%3e%3c/svg%3e");
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.1s ease;
}

.button__remove_active {
  opacity: 1;
  visibility: visible;
  user-select: all;
}

